// TODO decide on which header-search we wanna use, and then remove others

.video-container {
    max-height: 650px;
    overflow: hidden;
}
.video-container video {
    display: block;
}
.video-wrap {
    position: relative;
}
.video-wrap h1 {
    color: #FFF;
    font-size: 70px;
    text-transform: uppercase;
}
.video-wrap .gradient {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.1);
}
.video-text {
    position: absolute;
    top: 0;
    margin: 6% 0 0 4%;
    z-index: 1;
}

.promo {
    color: #FFFFFF;
}
.promo > .grid-container {
    background-image: url('/images/green-map.png');
    padding: 85px 0;
}
.promo--calendar {
    margin: 30px 0;
    text-transform: uppercase;
}
.promo--calendar-container {
    margin: 0 auto;
    position: relative;
}
.promo .uos-calendar {
    font-size: 130px;
}
.promo--calendar .date {
    font-family: $header-font-family;
    font-weight: 300;
    font-size: 50px;
    position: absolute;
    bottom: 0;
    left: 0;
    height: 80%;
    width: 100%;
}
.promo--calendar .month {
}
.promo--button {
    background-color: #FFFFFF;
    color: $heritage-green;
    font-weight: 700;
}

.research {
    background-image: url('/images/research-image.png');
    background-repeat: no-repeat;
    background-size: 100%;
    margin: 115px 0 200px;
    padding: 108px 0;
}
.research h1 {
    margin: 0 0 60px 0;
}
.research--article {
    background-color: #FFF;
    padding: 60px;
}
.research--article h2 {
    color: $heritage-green;
    font-size: 35px;
    margin: 0 0 50px 0;

}
.research .callout-buttons {
    padding-top: 0;
    padding-bottom: 0;
    position: absolute;
    left: 0;
    width: 100%;
    bottom: -170px;
}

.international {
}
.international .grid-x {
    max-height: 820px;
    position: relative;
}
.international--feature-image {
    bottom: 60px;
    position: relative;
    right: 360px;
}
.cell.international--content {
    background-color: #FFFFFF;
    font-size: 22px; 
    padding: 50px;
    position: absolute;
    bottom: 0;
}
.international--content p {
    text-transform: uppercase;
    line-height: 1.2;
}
.international--content .uos-globe {
    color: $energy-green;
    font-size: 72px;
    margin: 0 40px 0 0;
}
.counter {
    font-size: 130px;
}
.international--content sup {
    font-size: 50px;
    top: -1.2em;
}

.chevron-right {
    display: inline-block;
    margin: 0 0 0 10px;
    width: 10px;
}
.link {
    color: $heritage-green;
    font-size: 20px;
}