/***
HEADER STYLES

Styles the main header for the entire website.
***/

header {
	background-color: #FFFFFF;
    padding: 63px 0;
}
header .uos-magnifying-glass,
span .uos-magnifying-glass {
    color: #3e3e3e
}
.logo img {
    max-width: 300px;
}
@media screen and (max-width: 639px) {
    header {
        padding: 25px 0;
    }   
}
/***
Main Navigation - Primary

Styles the primary navigation sitting within the
main header of the site at the bottom.
***/
.primary-navigation {
    font-size: 22px;
    font-weight: 700;
}
.primary-navigation a {
    color: $heritage-green;
    position: relative;
    overflow: hidden;
    margin: 0;
    padding: 15px;;
}
.primary-navigation a:after {
    content: '';
    position: absolute;
    background-color: $heritage-green;
    bottom: 0;
    width: 100%;
    height: 5px;
    right: 100%;
    transition: right 0.3s cubic-bezier(.05, .69, .14, 1);
}
.primary-navigation a:hover:after {
    right: 0;
    transition: right 0.3s cubic-bezier(.05, .69, .14, 1);
}
/***
Main Navigation - Secondary

Styles the secondary navigation sitting within the
main header of the site at the top.
***/
.secondary-navigation a,
.secondary-navigation .uos-magnifying-glass {
    color: #95989A;
    font-size: 20px;
    font-weight: 600;
    padding: 10px;
}
.secondary-navigation .uos-magnifying-glass {
    font-size: 22px;
}

/***
INTERNAL HEADER STYLES

Styles the header for the internal pages of the website.
***/
.header--internal {
    background-color: #f6f5f4;
}
.header--internal a,
.header--internal .uos-magnifying-glass {
    color: #8a8a8a;
    font-size: 20px;
    font-weight: 600;
}
.header--internal .search-button { margin: 0 25px 0 15px; }
.header--internal .uos-magnifying-glass {
    font-size: 25px;
}

/***
BURGER MENU STYLES

Styles the burger menus.
***/
.burger {
    width: 50px;
    height: 41px;
    position: relative;
    cursor: pointer;
    margin: 0.7rem 0 0.7em 1rem;
}
.burger span {
    transition: width 0.1s ease-in-out 0.3s;
}
.burger span,
.burger span:before,
.burger span:after {
    width: 100%;
    height: 5px;
    background-color: $heritage-green; 
}
.burger span:before,
.burger span:after {
    content: '';
    position: absolute;
}
.burger span:before {
    top: 0;
    transform: rotate(0);
    transition: top 0.3s ease-in-out 0.3s, transform 0.3s ease-in-out;
}
.burger span:after {
    bottom: 0;
    transform: rotate(0);
    transition: bottom 0.3s ease-in-out 0.3s, transform 0.3s ease-in-out;
}
.burger.nav-is-open span {
    width: 0;
    transition: width 0.1s ease-in-out 0.3s;
}
.burger.nav-is-open span:before {
    top: 18px;
    transform: rotate(45deg);
    transition: top 0.3s ease-in-out, transform 0.3s ease-in-out 0.3s;
}
.burger.nav-is-open span:after {
    bottom: 18px;
    transform: rotate(-45deg);
    transition: bottom 0.3s ease-in-out, transform 0.3s ease-in-out 0.3s;
}
/***
INTERNAL HEADER MENU
***/
.header--internal-menu {
    background-color: #f6f5f4;
    display: none;
}
.hidden-links li {
    display: inline-block;
    margin: 0 20px 20px 0;
}
.hidden-links li:last-child {
    margin: 0 0 20px 0;
}
.header--internal-menu .internal-index-columns {
    border-top: 7px solid #D4D2CC;
    padding-top: 60px;
    padding-bottom: 30px;
}

.header--internal-menu a {
    color: #757774;
    display: inline-block;
    font-family: $header-font-family; 
    font-size: 26px;
    font-weight: 300;
    margin: 0 0 30px 0;
}
@media screen and (max-width: 639px) {
    .header--internal-menu a {
        color: $black;
        font-size: 18px;
        margin: 0 0 10px 0;
    }
    // .header--internal-menu h2 {
    //     color: $black;
    // }
    .internal-pages .header--internal-menu .internal-index-columns {
        column-count: 2;
    }
}