.accommodation-filter__filters {

    input[type='checkbox'] {
        position: absolute;
        left: -9999px;
    }

    input[type='checkbox'] + label {
        display: inline-block;
        padding: 10px;
        background: #eee;
        margin-bottom: 10px;
        margin-left: 0;
    }

    input[type='checkbox']:checked + label {
        background: #999;
        color: white;
    }
}

.accommodation-filter__properties {

    .title-block {
        margin-right: 10px;
    }

    .title-block:nth-child(3n+3) {
        margin-right: 0;
    }
}
