.search-button {
    cursor: pointer;
}

.header-search {
    display: none;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
	z-index: 10000;
}
.header-search .overlay {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: $black;
    // transition: 0.4s;
    opacity: 0.3;
    // z-index: 10000;
}
.header-search .content {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    background: $white;
    padding-top: 2rem;
    padding-bottom: 1rem;
    // z-index: 100000;
}
.header-search input[name='query'] {
    padding: 2rem 1rem;
    margin-bottom: 1rem;
    font-size: 1.5rem;
    font-weight: bold;
    color: $dark-gray;
}
.header-search input + button[type='submit'] {
    position: absolute;
    top: 10px;
    right: 20px;
    background: none;
    color: $black;
}

.header-search__wrapper {
    background: $white;
    display: none;
}

    .header-search__results {
        .columns {
            background: #fff;
            padding: 1rem;

            &:first-child {
                background: #eee;
            }
        }
        h3 {
            font-size: 100%;
            color: #999;
            margin-bottom: 1rem;
            text-decoration: underline;
        }
        h4 {
            font-size: 100%;
            margin-bottom: 0.5rem;
        }
        fieldset {
            margin-bottom: 1rem;
        }
        fieldset input {
            position: absolute;
            left: -9999px;
        }
        fieldset a {
            display: block;
            line-height: 2em;
        }
        fieldset input:focus + a {
            text-decoration: underline;
        }
    }

    .loader {
        border: 6px solid #eee; /* Light grey */
        border-top: 6px solid #999; /* Blue */
        border-radius: 50%;
        width: 50px;
        height: 50px;
        animation: spin 2s linear infinite;
        margin: 0 auto;
    }
.homepage-course-search__wrapper {
    display: none;
}
    @keyframes spin {
        0% { transform: rotate(0deg); }
        100% { transform: rotate(360deg); }
    }