/* COURSES */

.course-cta-header {
    .button {
        padding: 1rem;
        font-weight: bold;
    }
}

.tabs.tabs__courses-main {
    border: none;

    li {
        width: calc(100% / 5);
        a {
            text-align: center;
            border-left: 3px solid $white;
            background: $light-gray;
            padding: 1.7rem 1.5rem;
            font-weight: bold;
            &:hover {
                background: darken($light-gray, 10%);
            }
        }
        &:first-child a {
            border-left: none;
        }
        &.is-active a {
            background: $primary-color;
            color: $white;
        }
    }
}

.tabs.tabs__course-entry-requirements,
.tabs.tabs__course-fees-funding {
    border: none;
    border-bottom: 5px solid darken($light-gray, 10%);

    .tabs-title > a:focus,
    .tabs-title > a[aria-selected='true'] {
        font-weight: bold;
    }

    + .tabs-content {
        border: none;
    }
}

.tabs.tabs__course-details {
    border: none;
    background: darken($light-gray, 10%);

    li {
        width: calc(100% / 4);

        a {
            text-align: center;
            padding: 1.7rem 1.5rem;
            font-weight: bold;
            &:hover {
                background: darken($light-gray, 20%);
            }
        }
    }
    &.is-active a {
        background: $primary-color;
        color: $white;
    }

    .tabs-title > a:focus,
    .tabs-title > a[aria-selected='true'] {
        font-weight: bold;
        background: $primary-color;
        color: $white;
    }

    + .tabs-content {
        border: none;
        background: $light-gray;

        .accordion {
            background: none;

            .accordion-title {
                border: none;
                font-weight: bold;
                border-bottom: 5px solid $primary-color;
                padding: 1rem 0;
                font-size: 120%;
            }
            .accordion-content {
                padding-left: 0;
                padding-right: 0;
                border: none;
                background: none;

                tbody tr:nth-child(even) {
                    border-bottom: 0;
                    background: none;
                }

                tbody td {
                    background: $light-gray;
                    padding: 0.5rem 0 0.5rem 0;
                }
            }
        }
    }
}

.row__course-ranking {
    // margin-top: 1rem;
    // margin-bottom: 1rem;
    font-size: 150%;
    color: $dark-gray;

    .columns {
        padding: 0 3rem;
    }
}

.tabs-panel {
    &#course__overview,
    &#course__entry-requirements,
    &#course__details,
    &#course__fees-funding,
    &#course__after-graduation {
        padding: 0;
        margin: 0;
    }

    > .row {
        // @extend .row.collapse;
    }
}

#course-modules-tabs {
    + .tabs-content {
        .accordion-content {
            thead, tbody, tfoot {
                border: none;
            }
        }
    }
}

.course-combinations {
    table {
        @extend .text-left;
    }
}

.course-facilities-carousel,
#kis-widget {
    .slick-prev:before,
    .slick-next:before {
        color: $dark-gray;
        font-weight: bold;
        background: darken($light-gray, 10%);
        padding: 10px 15px;
        border-radius: 100%;
    }
    .slick-prev {
        left: -20px;
        &:before {
            content: '<';
        }
    }
    .slick-next {
        right: 0px;
        &:before {
            content: '>';
        }
    }
    .slick-slide img {
        display: inline-block;
    }
    .slick-dots li button:before {
        font-size: 20px;
    }
}

#kis-widget {
    .loader {
        border: 6px solid #eee; /* Light grey */
        border-top: 6px solid #999; /* Blue */
        border-radius: 50%;
        width: 50px;
        height: 50px;
        animation: spin 2s linear infinite;
        margin: 1rem auto;
    }

    @keyframes spin {
        0% { transform: rotate(0deg); }
        100% { transform: rotate(360deg); }
    }
}

.course-cta-footer {
    .button {
        margin-bottom: 0;
        padding: 1rem;
    }
}

#course__entry-requirements,
#course__fees-funding {
    .country-select {
        display: none;
    }

    .country-select,
    .country-select__tabs {
        padding: 1rem;
    }
}

.course__testimonial {
    blockquote{
        display:block;
        padding: 15px 20px 15px 55px;
        position: relative;
        font-size: 120%;
        line-height: 1.5;

        &::before{
            content: "\201C";
            font-family: Georgia, serif;
            font-size: 60px;
            font-weight: bold;
            color: #999;
            position: absolute;
            left: 10px;
            top: 0px;
        }

        &::after{
            content: "";
        }

        .author {
            font-size: 83%;
            margin-top: 1rem;
            font-weight: normal;
        }
    }
}

#course-tabs > .accordion-item {
    > .accordion-title {
        background: $light-gray;
        border: none;
    }

    > .accordion-content {
        border: none;
        padding: 0;
    }
}

