.search__active-filters {
    list-style: none;
    padding: 0;
    margin: 0;
    margin-bottom: 1rem;

    li {
        display: inline-block;
    }

    a,
    label {
        display: inline-block;
        padding: 10px;
        background: #eee;
        border: none;
        margin-right: 5px;
    }

    label {
        cursor: pointer;
    }
}

form.search__form {

    // TODO keep this? used for API only page
    .ordered_filters {
        margin-bottom: 1rem;

        label {
            display: inline-block;
            padding: 10px;
            background: #eee;
            border: none;
            margin-right: 5px;
            cursor: pointer;
        }
    }

    #search__filters {
        display: none;

        .filters-panel {
            padding-bottom: 1rem;
        }

        label {
            display: inline-block;
            background: #eee;
            border: none;
            margin-top: 10px;
            padding: 10px;
            cursor: pointer;
        }

        // input[type='checkbox'] {
        //     position: absolute;
        //     left: -999999px;
        // }

        label.is-active {
            background: #ccc;
        }

        input[type='checkbox'] {
            position: absolute;
            left: -9999px;
        }

        // label:has(input[type='checkbox']:checked) {
