    }
}

.search__results {

    .loader {
        border: 6px solid #eee; /* Light grey */
        border-top: 6px solid #999; /* Blue */
        border-radius: 50%;
        width: 50px;
        height: 50px;
        animation: spin 2s linear infinite;
        margin: 0 auto;
    }

    @keyframes spin {
        0% { transform: rotate(0deg); }
        100% { transform: rotate(360deg); }
    }

    .row {
        .columns  {
            margin-bottom: 1rem;

            h4 {
                font-size: 100%;
                font-weight: bold;
            }

            img {
                float: left;
            }

            .text {
                margin-left: 110px;
            }
        }
    }
}

#scholarship-search__results {
    .scholarship__table {
        .columns {
            background: $light-gray;
            padding: 1rem;
        }
        .deadline--not-available {
            background: $energy-dodger-blue;
        }
        .deadline--open {
            background: $primary-color;
        }
        .deadline--closing-soon {
            background: $energy-gold;
        }
        .deadline--closed {
            background: $energy-pink;
        }
    }
}

#testimonial-search__results {
    .testimonial {
        blockquote{
            display:block;
            background: #fff;
            padding: 15px 20px 15px 45px;
            margin: 0 0 20px;
            position: relative;

            /*Font*/
            font-family: Georgia, serif;
            font-size: 16px;
            line-height: 1.2;
            color: #666;
            text-align: justify;

            /*Borders - (Optional)*/
            border-left: 15px solid #c76c0c;
            border-right: 2px solid #c76c0c;

            /*Box Shadow - (Optional)*/
            -moz-box-shadow: 2px 2px 15px #ccc;
            -webkit-box-shadow: 2px 2px 15px #ccc;
            box-shadow: 2px 2px 15px #ccc;
        }

        blockquote::before{
            content: "\201C"; /*Unicode for Left Double Quote*/

            /*Font*/
            font-family: Georgia, serif;
            font-size: 60px;
            font-weight: bold;
            color: #999;

            /*Positioning*/
            position: absolute;
            left: 10px;
            top:5px;
            /*display: block;*/
        }

        blockquote::after{
            /*Reset to make sure*/
            content: "";
        }

        img {
            border-radius: 100%;
            -webkit-filter: grayscale(100%);
            filter: grayscale(100%);
            /*margin-bottom: 1em;*/
        }
    }
}



