.query-search-bar {
    background-color: #f6f5f4;
    border: none;
    box-shadow: none;
    color: #8a8a8a;
    font-size: 38px;
    font-weight: $global-weight-bold;
    max-height: 110px;
    padding: 56px 0;
}
.query-search-bar:focus {
    background-color: #f6f5f4;
}

.placeholder {
    color: $light-gray;
    height: 100%;
    left: 0;
    position: absolute;
    top: 0;
    width: 100%;
    pointer-events: none;
}
.placeholder--show {
    opacity: 1;
    transition: opacity 0.3s ease-in-out 0.3s;
}
.placeholder--hide {
    opacity: 0;
    transition: opacity 0.3s ease-in-out;
}
.placeholder p {
    // transform: scaleX(1);
    // transition: transform 0.3s ease 0.3s;
    font-size: 36px;
    color: $medium-gray;
}
.placeholder .uos-magnifying-glass {
    color: $energy-green;
    font-weight: 700;
    margin: 0 20px 0 0;
}
@media screen and (max-width: 639px) {
    .query-search-bar {
        padding: 32px 0;
    }
}