    .close_icon {
        position: absolute;
        right: 0;
        display: none;
        top: 40px;
        right: 3rem;
    }
.loader {
    border: 6px solid #eee;
    /* Light grey */

    border-top: 6px solid #999;
    /* Blue */

    border-radius: 50%;
    width: 50px;
    height: 50px;
    animation: spin 2s linear infinite;
    margin: 1rem auto;
}
#homepage-course-search input[name='query'] {
    // margin: 0;
    // background-color: $light-gray;
    // border: none;
    // font-size: 2.5rem;
    // // font-weight: bold;
    // position: relative;
    // color: $dark-gray;
    // min-height: 110px;
    // box-shadow: none;
    // color: transparent;
    &:focus {
        box-shadow: none;
    }
    &::-webkit-input-placeholder {
        /* WebKit, Blink, Edge */

        color: $dark-gray;
    }
    &:-moz-placeholder {
        /* Mozilla Firefox 4 to 18 */

        color: $dark-gray;
        opacity: 1;
    }
    &::-moz-placeholder {
        /* Mozilla Firefox 19+ */

        color: $dark-gray;
        opacity: 1;
    }
    &:-ms-input-placeholder {
        /* Internet Explorer 10-11 */

        color: $dark-gray;
    }
    &::-ms-input-placeholder {
        /* Microsoft Edge */

        color: $dark-gray;
    }
    + button {
        position: absolute;
        top: 0;
        padding: 0 1rem;
        right: 0;
        line-height: 5rem;
        font-size: 2.5rem;
        color: $dark-gray;
        cursor: pointer;
    }
}
.search-box__results {
    padding: 4rem;
    background: $white;
    margin-bottom: 3rem;
    .tabs, .tabs + .tabs-content, .tabs-title > a:hover {
        background: none;
    }
    .tabs-panel {
        padding: 0;
    }
    .filters {
        margin-bottom: 1rem;
        position: relative;
        label {
            display: inline-block;
            // padding: 10px;
            // background: #eee;
            border: none;
            margin-right: 50px;
            cursor: pointer;
            font-size: 1.2rem;
            color: $dark-gray;
        }
        input[type='checkbox'] {
            // position: absolute;
            // left: -999999px;
            margin: 0;
        }
        label.is-active {
            // background: #ccc;
        }
        .view_all {
            position: absolute;
            right: 0;
            font-size: 1.2rem;
        }
    }
    fieldset {
        margin-bottom: 1rem;
        padding-bottom: 0;
    }
    fieldset input {
        position: absolute;
        left: -9999px;
    }
    fieldset h3 {
        font-size: 120%;
        font-weight: bold;
        margin-top: 1rem;
    }
    fieldset input:focus + a {
        text-decoration: underline;
    }
    fieldset input:focus + a.button {
        // @extend .button:hover;
        text-decoration: none;
    }
    a.result {
        font-size: 1.5rem;
        font-weight: bold;
    }
    .button {
        margin-bottom: 0;
        padding: 2rem;
        font-size: 1.5rem;
        background: $dark-gray
    }
}
.search-box__loading {
    padding-bottom: 2rem;
}

.homepage-course-search__query:focus + .placeholder p {
    transform: scaleX(0);
    transition: transform 0.3s ease;
}
input#homepage-course-search__query:focus {
    color: #8a8a8a;
}
