/***
SITE WIDE STYLES
***/
body {
	// color: #3e3e3e;
	// font-family: 'Open Sans', sans-serif;
}

ul,
ul ul,
li {
    list-style: none;
    margin: 0;
    padding: 0;
}
form,
input {
    outline: none;
    position: relative;
}
input {
    border: none;
    width: 100%;
}
[type='text']:focus, [type='password']:focus,
[type='date']:focus, [type='datetime']:focus,
[type='datetime-local']:focus, [type='month']:focus,
[type='week']:focus, [type='email']:focus,
[type='number']:focus, [type='search']:focus,
[type='tel']:focus, [type='time']:focus, 
[type='url']:focus, [type='color']:focus,
textarea:focus {
    background-color: inherit;
    border: none;
    box-shadow: none;
}
/***
BUTTONS

Styles all buttons used throughout the site.
***/
/*
Back to main website button
*/
a.back-to-main-site {
    color: $heritage-green;
    display: inline-block;
    font-family: $header-font-family;
    font-size: 26px;
    font-weight: $header-font-weight;
}
@media screen and (max-width: 639px) {
    a.back-to-main-site {
        font-size: 22px;
        margin: 20px 0 0 0;
    }
}
/***
HOOK STYLES

Styles a hook under the content wrapped with the class '.hook'.
***/
.hook {
    display: inline-block;
    position: relative;
}
.hook-right {
    padding: 0 20px 20px 0;
}
.hook-left {
    padding: 0 0 20px 20px;
}
.hook:before,
.hook:after {
    background-color: currentColor;
    bottom: 0;
    content: '';
    position: absolute;
}
.hook:before {
    height: 25px;
    width: 8px;
}
.hook-right:before {
    right: 0;
}
.hook-left:before {
    left: 0;
}
.hook:after {
    height: 8px;
    left: 0;
    width: 100%;
}
/***
HIGHLIGHT STYLES

Styles a short highlight line under the content wrapped with the class '.higlight'.
***/
.highlight {
    color: $heritage-green;
    padding: 0 0 30px 0;
    position: relative;
}
.highlight:after {
    content: '';
    background-color: $energy-green;
    bottom: 0;
    left: 0;
    position: absolute;
    width: 67px;
    height: 8px;
}
// .button-callout
// Styles the featured buttons on the homepage.
.callout-buttons {
    padding-top: 60px;
    padding-bottom: 70px;
}
.button-callout {
    font-size: 20px;
    min-width: 350px;
    max-width: 350px;
    padding: 33px 50px;
}


