/* KITCHEN SINK COMPONENTS */

/* background-colours */
.bg-energy-green {
    background-color: $energy-green;
}
.bg-heritage-green {
    background-color: $heritage-green;
}
.bg-energy-pink {
    background-color: $energy-pink;
}
.bg-light {
    background-color: #F5F5F5;
}
.bg-dark {
    background-color: #404040;
}
.bg-primary {
    background-color: $primary-color;
    color: $white;
}
.bg-secondary {
    background-color: $secondary-color;
    color: $white;
}
.bg-dark-gray {
    background-color: $dark-gray;
    color: $white;
}
.bg-gray {
    background-color: darken($light-gray, 10%);
    color: $black;
}
.bg-light-gray {
    background-color: $light-gray;
    color: $black;
}
.bg-success {
    background-color: $success-color;
    color: $white;
}
.bg-alert {
    background-color: $alert-color;
    color: $white;
}
.bg-warning {
    background-color: $warning-color;
    color: $white;
}

// TODO add these to kitcen sink docs
.vertical-margin-1 {
    margin-top: 1rem;
    margin-bottom: 1rem;
}
.vertical-margin-2 {
    margin-top: 2rem;
    margin-bottom: 2rem;
}
.vertical-margin-3 {
    margin-top: 3rem;
    margin-bottom: 3rem;
}

.vertical-padding-1 {
    padding-top: 1rem;
    padding-bottom: 1rem;
}
.vertical-padding-2 {
    padding-top: 2rem;
    padding-bottom: 2rem;
}
.vertical-padding-3 {
    padding-top: 3rem;
    padding-bottom: 3rem;
}

.image-full-width {
    width: 100%;
    height: auto;
}


/* Back To Top Button */

.scroll-to-top {
    background-color: #1779ba;
    border: 3px solid transparent;
    bottom: 20px;
    color: #fefefe;
    font-weight: bold;
    opacity: 0;
    padding: 10px;
    position: fixed;
    right: 20px;
    text-align: center;
    text-decoration: none;
    transition: all 0.4s ease;
}
.scroll-to-top:hover {
    background-color: #fefefe;
    border: 3px solid #1779ba;
    color: #1779ba;
}
.scroll-to-top:focus {
    color: #fefefe;
    outline: none;
}
.scroll-to-top:hover:focus {
    color: #1779ba;
}

/* Reveal (modals) */

.reveal.reveal__simple {
    padding: 0;
    color: $dark-gray;

    .title {
        background: $light-gray;
        padding: 2rem 3rem;
    }

    .content {
        padding: 1rem 3rem;
    }

    .close-button {
        top: 1.5rem;
        right: 2rem;
    }
}


/* Youtube videos */

.yt-responsive-container {
    position: relative;
    width: 100%;
    height: 0;
    padding-bottom: 56.25%;
    margin-bottom: 1rem;

    iframe {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
    }
}


/* Accordion */

.accordion.accordion__flat {
    background: none;

    .accordion-content {
        background: none;
        padding: 0.5rem 1rem;
    }
    .accordion-title {
        border-bottom: 4px solid $primary-color;
        padding-left: 0;
        padding-right: 0;
        margin-left: 1.25rem;
        margin-right: 1.25rem;

        &::before {
            content: '';
        }
    }
    .dividing-text {
        background: $medium-gray;
        padding: 1rem;
        margin-bottom: 1rem;
        text-align: center;
    }
}

/* Tabs */
.no-pad-left {
    padding-left: 0;
}
.no-pad-right {
    padding-right: 0;
}
.tabs__plain {
    border: none;

    li {
        &.is-active {
            a {
                font-weight: bold;
                text-decoration: underline;
            }
        }
    }

    +div.tabs-content {
        border: none;
        margin-bottom: 1em;
    }
}

.tabs__flat {
    border: none;
    border-bottom: 3px solid $medium-gray;
    background: $light-gray;
    // margin-bottom: 0;

    li {
        &.is-active {
            a {
                font-weight: bold;
            }
        }
    }

    +div.tabs-content {
        background: $light-gray;
        border: none;
        margin-bottom: 1em;

        dd {
            border-bottom: 2px solid $medium-gray;
        }
    }
}

.tabs__bold {
    border: none;
    // margin-bottom: 1em;

    li {
        // width: 50%;
        text-align: center;

        a {
            background: $medium-gray;

            &:hover {
                background: $medium-gray;
            }
        }

        &.is-active {
            a {
                background: $primary-color;
                color: $white;
                font-weight: bold;
            }
        }
    }

    +div.tabs-content {
        border: none;
        margin-bottom: 1em;
    }
}

.tabs__bold--1-tabs {
    li {
        width: 100%;
    }
}
.tabs__bold--2-tabs {
    li {
        width: 50%;
    }
}
.tabs__bold--3-tabs {
    li {
        width: 33.33333333%;
    }
}
.tabs__bold--4-tabs {
    li {
        width: 25%;
    }
}
.tabs__bold--5-tabs {
    li {
        width: 20%;
    }
}

// this will break tabs on small devices
.tabs--break-for-small {
    @media screen and #{breakpoint(small only)} {
        display: none;
    }
}

.tabs-content--no-padding {
    >.tabs-panel {
        padding: 0;
    }
}

.tabs-content--break-for-small {
    @media screen and #{breakpoint(small only)} {
        border: none;

        .tabs-panel {
            display: block;
        }
    }
}


/* Figure blocks */

.title-block {
    // @extend .columns;
    // float: left !important;
    margin-bottom: 1em;
    position: relative;

    img {
        width: 100%;
        height: auto;
    }

    .title-block__title {
        text-align: center;
        display: block;
        padding: 10px;
        background: $primary-color;
        color: $white;
    }

    &.title-block--overlay {
        figcaption {
            bottom: 0;
        	width: 100%;
        	position: absolute;
        }
    }
}

/* Statistic Block */
.statistic {
  color: #756E64;
  text-transform: uppercase;
}
.statistic:last-child {
  border-bottom: 12px solid #A1C85E;
}
.statistic__info {
  font-size: 22px;
  margin: 0;
}
.statistic__info--stat {
  font-size: 130px;
  line-height: 1.5;
  margin: 0
}

/* Block Quote */
blockquote {
  border-left: none;
  margin: 0;
  padding: 0;
}
cite {
  font-style: normal;
}
cite:before {
  content: '';
}
.quote-block {
  border-top: 12px solid #A1C85E;

}
.quote-block__copy {
  font-size: 32px;
  line-height: 1.5;
  padding-top: 30px;
}
.quote-block__cite {
  border-top: 4px solid #A1C85E;
  font-size: 20px;
  font-weight: 700;
  margin-top: 20px;
  padding-top: 10px;
  text-transform: uppercase;
}
.quote-block__cite span {
  color: #396A33;
}

/* Promo Block */
span[class^="uos-"] {
    font-size: 32px;
}
.promo-box.text-left span,
.promo-box.text-left h2 {
    display: inline;
}




