/***
INTERNAL HOMEPAGE

Styles the internal homepage for the website.
***/
.internal-pages .intro {
	margin-top: 60px;
}
.internal-pages .intro h1 {
	color: $heritage-green;
	font-size: 36px;
}
.internal-pages .intro p {
	font-size: 26px;
}
.internal-search-bar {
	margin: 60px 0;
}
.internal-index {
	padding: 0 0 60px;
}
.internal-pages .internal-index-columns {
	column-count: 3;
}
.internal-pages ul {
	// margin: 0 0 35px 0;
	-webkit-column-break-inside: avoid;
	          page-break-inside: avoid;
	               break-inside: avoid;
}
.internal-index h2 {
	color: $black;
	font-family: $header-font-family;
	font-size: 26px;
	font-weight: 300;
	max-width: 75%;
}
.internal-pages .internal-index a:hover {
	color: #006938;
}
.internal-pages .placeholder .uos-magnifying-glass {
	color: $heritage-green;
}
@media screen and (max-width: 1024px) {
	.internal-links .internal-index-columns {
		column-count: 2;
	}
}
@media screen and (max-width: 873px) {
	.internal-index .internal-index-columns {
		column-count: 1;
	}
	.internal-index h2 {
		max-width: none;
	}
}
/***
FOOTER ACCORDION

Styles the accordion used within the footer for the entire website on mobile.
***/
.accordion-section {
	margin: 0 0 40px;
}
.accordion-column {
	position: relative;
}
.accordion-column-section-state {
	display: none;
}
.accordion-column-section { display: inline-block; }
.accordion-column-section-title {
	background-color: #ffffff;
    padding-top: 10px;
    padding-bottom: 10px;
    position: relative;
    user-select: none;
    z-index: 2;
}
.accordion-column-section-title:before {
	content: '';
}
.accordion-column-section-item a {
	color: #8e8e8e;
}
@media screen and (max-width: 639px) {
	.internal-search-bar {
		margin: 30px 0 0;
	}
	.internal-pages .intro h1 {
		font-size: 28px;
	}
	.internal-pages .intro p {
		font-size: 18px;
	}
	.internal-pages .placeholder .uos-magnifying-glass {
		font-size: 20px;
	}
	/***
	FOOTER ACCORDION
	***/
	.accordion-section {
		border-bottom: 1px solid $black;
		margin: 0;
		padding: 25px 0;
	}
	.accordion-column-section {
		display: block;
	}
	.accordion-column-section-title {
		cursor: pointer;
	}
	.accordion-column-section-title:before {
		content: '+';
		color: $heritage-green;
		position: absolute;
		right: 0;
		transform: rotate(0deg);
		transition: transform 0.3s ease;
	}
	.accordion-column-section-state:checked+.accordion-column-section .accordion-column-section-title:before {
		transform: rotate(45deg);
	}
	.accordion-column-section-list {
		margin: 0;
	    overflow: hidden;
	    padding-top: 5px;
	    padding-bottom: 16px;
	    position: absolute;
	    top: 100%;
	    left: 0;
	    width: 100%;
	    visibility: hidden;
	    z-index: -1;
	    transform: translateY(-60px);
	}
	.accordion-column-section-state:checked+.accordion-column-section .accordion-column-section-list {
		position: static;
	    visibility: visible;
	    z-index: 1;
	    -webkit-transform: none;
	    -ms-transform: none;
	    transform: none;
	    transition: transform 0.3s ease;
		transform: translateY(0px);
	}
}
/***
INTERNAL OVERVIEW PAGE

Styles the internal overview pages for the website.
***/
.internal-sub-section-navigation {
	background-color: #F6F5F4;
	border-left: 7px solid $energy-green;
	padding: 70px 70px 60px;
}
.internal-sub-section-navigation ol {
	counter-reset: item;
	-webkit-column-count: 2;
	column-count: 2;
	margin: 0;
}
.internal-sub-section-navigation li {
	margin: 0 0 15px 0;
}
.internal-sub-section-navigation li a {
	color: $black;
}
.internal-sub-section-navigation li:before {
	counter-increment: item;
    content: counter(item)". ";
}

.internal-sidebar-navigation h2 {
	color: $heritage-green;
	font-family: $header-font-family;
	font-size: 26px;
	font-weight: 300;
}
.internal-sidebar-navigation a {
	color: #746E64;
	display: block;
	margin: 0 0 10px 0;
}
.internal-sidebar-navigation li {
	position: relative;
}
.internal-sidebar-navigation li.active {
	color: $heritage-green;
}
.internal-sidebar-navigation li.active:before {
	content: '\2022';
	position: absolute;
	left: -20px;
}
.internal-sidebar-navigation li.active a {
	color: $heritage-green;
	font-weight: 700;
}