// the following classes can be extended by other classes

$colors: (
    // Tertiary Colours
    light-mink: #c5bfb7,
    medium-mink: #776e64,
    dark-mink: #373a36,

    // Energy Colours
    energy-green: #76bd22,
    energy-powder-green: #9cbeac,
    energy-cyan: #008996,
    energy-gold: #f4c400,
    energy-orange: #ee7624,
    energy-sky-blue: #5fb3e5,
    energy-dodger-blue: #417bbe,
    energy-pink: #d31668,
    energy-purple: #592c82,

    // Heritage Colours
    heritage-green: #006938,
    heritage-sea-green: #2b7152,
    heritage-dark-cyan: #005e64,
    heritage-gold: #ecaa01,
    heritage-orange: #e14612,
    heritage-slate-blue: #3a5ea8,
    heritage-indigo: #1b4488,
    heritage-magenta: #9d1e65,
    heritage-purple: #312665,
);

$variations: (
    30, 50, 70,
);

// this will generate classes that we will use in the kitchen
// sink e.g. energy-green, energy-green--30, energy-green--50, ...
@each $name, $hex in $colors {
    .bg-#{$name} {
      background-color: $hex;
    }
    @each $v in $variations {
      $perc: percentage((100-$v)/100);
      .bg-#{$name}--#{$v} {
        background-color: tint($hex, $perc);
      }
    }
}
