/***
FOOTER STYLES

Styles the main footer for the entire website.
***/
footer {
	background: #000000;
	color: $white;
	overflow: hidden;
	padding: 100px 0;
}
footer h1 {
	font-family: $body-font-family;
}
footer .highlight {
	color: $white;
	font-size: 22px;
	font-weight: 600;
	margin: 0 0 30px 0;
	padding: 0 0 40px 0;
}
footer .highlight:after {
	background-color: $white;
	height: 4px;
	width: 100px;
}
footer a {
	color: $white;
	display: block;
	font-size: 18px;
	transition: color 0.2s ease-in-out;
}
footer a:hover {
	color: $energy-green;
}
/***
FOOTER SOCIAL LINKS
***/
.footer-social-links {
	margin: 33px 0 0 0;
}
.footer-social-links a {
	display: inline-block;
	margin: 0 10px 0 0;
}
.footer-social-links a:last-child {
	margin: 0;
}
.footer-social-links a.facebook:hover { color: #3b5998; }
.footer-social-links a.twitter:hover { color: #55acee; }
.footer-social-links a.instagram:hover { color: #125688; }
.footer-social-links a.linkedin:hover { color: #007bb5; }
.footer-social-links a.vimeo:hover { color: #aad450; }
.footer-social-links a.snapchat:hover { color: #fffc00; }

.footer-hook {
	padding: 0 0 22px;
	width: 100%;
}
.footer-hook h2 {
	padding: 0 0 21px 21px;
	position: relative;
	text-transform: uppercase;
}
.footer-hook h2:before,
.footer-hook h2:after {
	background-color: currentColor;
	bottom: 0;
	content: '';
	left: 0;
	position: absolute;
}
.footer-hook h2:before {
	height: 21px;
	width: 5px;
}
.footer-hook h2:after {
	height: 5px;
	width: 200%;
}
/***
FOOTER MAP
***/
.footer-map-image {
	max-height: 325px;
	max-width: 224px;
}
/***
FOOTER ACCREDITATIONS
***/
.athena-swan {
	margin: 0 40px 0 0
}
.quality-assured {
	margin: 0 12px 0 0;
}
/***
STRAPLINE
***/
footer .strapline {
	position: relative;
}
/***
FOOTER ACCORDION

Styles the accordion used within the footer for the entire website on mobile.
***/
.footer-accordion-column {
	position: relative;
}
.footer-accordion-column-section-state {
	display: none;
}
.footer-accordion-column-section-title {
	background-color: #000000;
	font-family: $body-font-family;
    padding-top: 10px;
    padding-bottom: 10px;
    position: relative;
    user-select: none;
    z-index: 2;
}
.footer-accordion-column-section-title:before {
	content: '';
}
/***
FOOTER TABLET STYLES
***/
@media screen and (max-width: 1023px) {
	.footer-accordion-column {
		margin: 0 0 40px 0;
	}
}
/***
FOOTER MOBILE STYLES
***/
@media screen and (max-width: 639px) {
	.footer--column {
		margin: 0 0 30px 0;
	}
	.footer-hook h2 {
		font-size: 32px;
	}
	.footer-hook {
		padding: 0;
		position: relative;
	}
	.footer-map {
		text-align: center;
	}
	.footer-map-image {
		margin: 70px 0;
	}
	/***
	FOOTER SCOIAL LINKS
	***/
	.footer-social-links {
		margin: 73px 0 15px 0;
	}
	/***
	FOOTER ACCREDITATIONS
	***/
	.athena-swan {
		margin: 0 40px 30px 0
	}
	.quality-assured {
		margin: 0 12px 30px 0;
	}
	/***
	FOOTER ACCORDION
	***/
	.footer-accordion-column-section-title {
		cursor: pointer;
	}
	.footer-accordion-column-section-title:before {
		content: '+';
		position: absolute;
		left: 110px;
		transform: rotate(0deg);
		transition: transform 0.3s ease;
	}
	.footer-accordion-column-section-state:checked+.footer-accordion-column-section .footer-accordion-column-section-title:before {
		transform: rotate(45deg);
	}
	.footer-accordion-column-section-list {
		margin: 0;
	    overflow: hidden;
	    padding-top: 5px;
	    padding-bottom: 16px;
	    position: absolute;
	    top: 100%;
	    left: 0;
	    width: 100%;
	    visibility: hidden;
	    z-index: -1;
	    transform: translateY(-100px);
	}
	.footer-accordion-column-section-state:checked+.footer-accordion-column-section .footer-accordion-column-section-list {
		position: static;
	    visibility: visible;
	    z-index: 1;
	    -webkit-transform: none;
	    -ms-transform: none;
	    transform: none;
	    transition: transform 0.3s ease;
		transform: translateY(0px);
	}
}
